	onReady() {
		this.getList();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		if (this.isload == false) {
			this.pageNum++;
			this.getList();
		}
	},
	onLoad() {},
	methods: {

		getList() {
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: "https://xiaoce-timeline-api-ms.juejin.im/v1/getListByLastTime",
				"method": "GET",
				"data": {
					pageNum: this.pageNum
				},
				success: (res) => {
					if (res.data.d.length != 0) {
						this.list = this.list.concat(res.data.d)
						this.isload = false
					} else {
						this.isload = true
					}
					uni.hideLoading()
				}
			})
		},

		//扫一扫
		scan() {
			uni.scanCode({
				success: (res) => {
					uni.showToast({
						title: '条码内容：' + res.result
					});
				}
			});
		},
		//搜索跳转
		toSearch() {
			uni.navigateTo({
				url:"../search/search"
			})
		},
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({
				title: e.src
			});
		},
		//分类跳转
		toCategory(e) {
			uni.showToast({
				title: e.title
			});
		},
		//推荐商品跳转
		toPick(e) {
			uni.showToast({
				title: '推荐商品' + e.goods_id
			});
		},
		//商品跳转
		toGoods(e) {
			uni.showToast({
				title: '商品' + e.goods_id
			});
		},
		//更新分类指示器
		categoryChange(event) {
			this.currentPageindex = event.detail.current;
		},
		//分类图片加载完毕
		categoryImgLoad(e) {
			this.categoryImg = this.categoryImg ? this.categoryImg + 1 : 1;
			//完成加载11个分类图片开始计算分类高度，若分类图片不足10个则修改此处的10。
			if (this.categoryImg == 10) {
				this.getCategoryHeight();
			}
		},
		//更新分类高度
		getCategoryHeight() {
			let view = uni.createSelectorQuery().select('.category-list');
			view.fields({
					size: true
				},
				data => {
					this.categoryHeight = data.height + 'px';
				}
			).exec();
		}
	},
	components: {
		WaterfallFlow
	}
};
